import React, { Fragment } from "react";
import { Button } from "antd";
import { Form, FormContentBase } from "components/ui/Form/Form";
import {
    ModalContent,
    ModalOpenButton,
    useModalContext,
} from "components/ui/Modal";
import { Module, AccessControlTypes } from "types";

export default function ModuleModal() {
    const { onOk, form } = useModalContext();

    function handleOk() {
        onOk("/module", "create", {} as Module);
    }

    return (
        <Fragment>
            <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
                <Button children="Create New Module" />
            </ModalOpenButton>
            <ModalContent title="Create new module" onClickOk={handleOk} formName="create_module_form">
                <Form schemaName="module">
                    <FormContentBase
                        name="create_module_form"
                        form={form}
                        preserve={false}
                    />
                </Form>
            </ModalContent>
        </Fragment>
    );
}
