import React from "react";
import { PageHeader, Spin, Space, Button, Table, Collapse, Input, Select, message, Tooltip } from "antd";
import { useMutation, useQuery } from "react-query3";
import { Feature, Module } from 'types';
import { DownloadOutlined, LinkOutlined } from "@ant-design/icons";
import { Modal } from "components/ui/Modal";
import ModulesModal from "./newModuleModal"
import EditModal from "components/ui/EditModal";
import DeleteButton from "components/ui/DeleteButton";
import EditorButton from "components/ui/EditorButton";
import useAppStore from "components/context/app/Hooks";
import NotLicensed from "components/pages/notLicensed";
import axios from "axios";
import queryClient from "components/utils/queryClient";

const { Option } = Select;

function groupBy(list, keyGetter) {
    const map = new Map();
    list.forEach((item) => {
        const key = keyGetter(item);
        const collection = map.get(key);
        if (!collection) {
            map.set(key, [item]);
        } else {
            collection.push(item);
        }
    });
    return map;
}

function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
}

const columns = [
    {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        onFilter: (value, record) => record.name.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.name.localeCompare(b.name),
        width: 100,
    },
    {
        title: 'Version',
        dataIndex: 'version',
        key: 'version',
        onFilter: (value, record) => record.version.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.version.localeCompare(b.version),
        width: 100,
    },
    {
        title: 'Path',
        dataIndex: 'path',
        key: 'path',
        onFilter: (value, record) => record.path.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.path.localeCompare(b.path),
        width: '60%',
    },
    {
        title: 'Source',
        dataIndex: 'source',
        key: 'source',
        render: (text, record) => {
            if (record.source === 1) {
                return <span>PowerShell Gallery</span>
            }
            return <span>Local</span>
        }
    },
    {
        width: "fit-content",
        dataIndex: "actions",
        render: (text, record) => {
            if (record.readOnly) {
                return <></>
            }

            return (
                <Space>
                    {record.source === 0 && <Modal>
                        <EditModal record={record} allowedWithOneWayGitSync={false} />
                    </Modal>}
                    <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
                    {record.source === 0 && <EditorButton
                        allowedWithOneWayGitSync={false}
                        accessControls={record?.accessControls}
                        to={"/admin/platform/module/" + record?.id}
                    />}
                </Space>
            );
        },
    },
];

const LocalModules = ({ search }) => {
    const { data: modules, isLoading } = useQuery<Module[]>("/module");

    if (isLoading) {
        return <Spin />
    }

    const filteredModules = search === '' ? modules : modules.filter(m => m.name.toLowerCase().includes(search.toLowerCase()));

    const keys = modules.filter(m => m.readOnly).map(m => m.environment).filter(onlyUnique);
    const moduleGroups = groupBy(filteredModules, m => m.environment);

    const psuModules = modules.filter(m => !m.readOnly);

    return (
        <Space direction="vertical" style={{ width: '100%' }}>
            <Collapse accordion>
                {keys.map(key => {
                    var modules = moduleGroups.get(key);
                    return (
                        <Collapse.Panel header={`${key} (${modules ? modules.length : 0} modules)`} key={key}>
                            <Table key={key} dataSource={modules} columns={columns} tableLayout="fixed" />
                        </Collapse.Panel>
                    )
                })}
                {
                    psuModules.length > 0 && <Collapse.Panel header={`Universal Modules (${psuModules ? psuModules.length : 0} modules)`} key="psuModules">
                        <Table key="psuModulesTable" dataSource={psuModules} columns={columns} tableLayout="fixed" />
                    </Collapse.Panel>
                }
            </Collapse>
        </Space>
    )
}

const InstallModuleButton = ({ name, version }) => {
    const { mutateAsync: installModule, isLoading } = useMutation(
        async (module: { name: string; version: string }) =>
            await axios.post(
                `/api/v1/module/save`,
                module
            ),
        {
            onSuccess: (...args) => {
                message.success("Saved module!");
                queryClient.refetchQueries("/dashboard/marketplace");
            },
        }
    );

    return (<Tooltip title="Install">
        <Button
            type="text"
            icon={<DownloadOutlined />}
            onClick={() => installModule({ name, version })}
            loading={isLoading}
        />
    </Tooltip>)

}

const galleryColumns = [
    {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        onFilter: (value, record) => record.name.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.name.localeCompare(b.name),
        width: 100,
    },
    {
        title: 'Version',
        dataIndex: 'version',
        key: 'version',
        onFilter: (value, record) => record.version.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.version.localeCompare(b.version),
        width: 100,
    },
    {
        width: "fit-content",
        dataIndex: "actions",
        render: (text, record) => {
            return (
                <InstallModuleButton name={record.name} version={record.version} />
            );
        },
    },
];

const GalleryModules = ({ search }) => {
    const { data: modules, isLoading } = useQuery<Module[]>(`/module/find/${search}`, {
        enabled: !!search
    });

    return (
        <Space direction="vertical" style={{ width: '100%' }}>
            <Table dataSource={modules} columns={galleryColumns} loading={isLoading} />
        </Space>
    )
}

export default function Modules() {
    const { licensed } = useAppStore();
    const [search, setSearch] = React.useState('');
    const [scope, setScope] = React.useState('local');

    if (!licensed(Feature.Automation)) {
        return <NotLicensed title="Modules are only available for licensed instances." />
    }

    return (
        <PageHeader title="Modules" subTitle="Manage PowerShell Modules for your PowerShell Universal environment."
            extra={
                [
                    scope === "local" && <Modal>
                        <ModulesModal />
                    </Modal>,
                    <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/platform/modules" target="_blank">Documentation</Button>
                ]
            }>

            <Input.Group compact>
                <Select defaultValue="local" style={{ width: '15%' }} onChange={e => setScope(e)}>
                    <Option value="local">Local</Option>
                    <Option value="psgallery">PowerShell Gallery</Option>
                </Select>
                <Input.Search allowClear onSearch={e => setSearch(e)} placeholder="Search" style={{ width: '85%' }} />
            </Input.Group>
            {scope === "local" && <LocalModules search={search} />}
            {scope === "psgallery" && <GalleryModules search={search} />}
        </PageHeader>

    );
}
